import React, { useEffect, useState } from "react";

const TransactionHistory = ({ stakingContract, signer }) => {
  const [transactions, setTransactions] = useState([]);

  useEffect(() => {
    const fetchTransactions = async () => {
      if (!stakingContract || !signer) return;

      try {
        const address = await signer.getAddress();
        const stakeFilter = stakingContract.filters.Staked(address, null, null);
        const stakeEvents = await stakingContract.queryFilter(stakeFilter);
        const formattedEvents = stakeEvents.map(event => ({
          type: "Stake",
          amount: event.args.amount.toString(),
          timestamp: new Date(Number(event.args.timestamp) * 1000).toLocaleString(),
        }));
        setTransactions(formattedEvents);
      } catch (error) {
        console.error("Error fetching transactions:", error);
      }
    };

    fetchTransactions();

    // Listen for new stakes
    stakingContract.on(stakingContract.filters.Staked(), fetchTransactions);

    return () => {
      stakingContract.off(stakingContract.filters.Staked(), fetchTransactions);
    };
  }, [stakingContract, signer]);

  return (
    <div className="card">
      <h3 className="text-xl font-bold">Transaction History</h3>
      <ul className="mt-4">
        {transactions.length === 0 ? <li>No transactions found.</li> : transactions.map((tx, index) => (
          <li key={index}>{tx.type}: {tx.amount} STK at {tx.timestamp}</li>
        ))}
      </ul>
    </div>
  );
};

export default TransactionHistory;
