import React, { useEffect, useState } from "react";

const StakingInfo = ({ stakingContract, signer }) => {
  const [stakingBalance, setStakingBalance] = useState("0");
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchStakingBalance = async () => {
      if (!stakingContract || !signer) {
        console.warn(" Staking contract or signer not available yet.");
        return;
      }

      try {
        setLoading(true);
        console.log(" Fetching staking balance...");
        const address = await signer.getAddress();
        const rawBalance = await stakingContract.balances(address);
        setStakingBalance(rawBalance.toString());
        console.log(" Staking Balance Fetched:", rawBalance.toString());
      } catch (error) {
        console.error(" Error fetching staking balance:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchStakingBalance();
  }, [stakingContract, signer]);

  return (
    <div className="card p-6">
      <h3 className="text-xl font-bold mb-4">Staking Info</h3>
      {loading ? <p> Loading...</p> : <p>Staking Balance: <strong>{stakingBalance} STK</strong></p>}
    </div>
  );
};

export default StakingInfo;
