import React, { useState } from "react";
import { Contract, parseUnits } from "ethers";

const StakeWithdraw = ({ stakingContract, signer }) => {
  const [stakeAmount, setStakeAmount] = useState("");
  const [loading, setLoading] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");

  const handleStake = async () => {
    try {
      if (!stakingContract || !stakeAmount) {
        throw new Error("Staking contract or amount missing.");
      }

      setLoading(true);

      // Fetch staking token address
      const tokenAddress = await stakingContract.stakingToken();
      const tokenContract = new Contract(
        tokenAddress,
        [
          "function approve(address spender, uint256 amount) public returns (bool)",
          "function allowance(address owner, address spender) public view returns (uint256)",
          "function balanceOf(address owner) public view returns (uint256)",
          "function decimals() public view returns (uint8)",
        ],
        signer
      );

      // Convert amount
      const decimals = await tokenContract.decimals();
      const amountInWei = parseUnits(stakeAmount, decimals);

      // Check user balance
      const userAddress = await signer.getAddress();
      const userBalance = await tokenContract.balanceOf(userAddress);
      if (userBalance.lt(amountInWei)) {
        throw new Error("Insufficient balance.");
      }

      // Approve if necessary
      const allowance = await tokenContract.allowance(userAddress, stakingContract.address);
      if (allowance.lt(amountInWei)) {
        const approveTx = await tokenContract.approve(stakingContract.address, amountInWei);
        await approveTx.wait();
      }

      // Stake
      const stakeTx = await stakingContract.stake(amountInWei);
      await stakeTx.wait();
    } catch (error) {
      setErrorMessage(error.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="card p-6">
      <h3 className="text-xl font-bold">Stake Tokens</h3>
      <input
        type="number"
        placeholder="Enter amount to stake"
        value={stakeAmount}
        onChange={(e) => setStakeAmount(e.target.value)}
        className="w-full p-4 mb-4 bg-transparent border border-purple-500 rounded-lg text-white"
      />
      <button
        className="w-full bg-gradient-to-r from-purple-600 to-blue-500 py-3 rounded-lg text-lg font-bold"
        onClick={handleStake}
        disabled={loading}
      >
        {loading ? "Staking..." : "Stake"}
      </button>
      {errorMessage && <p className="text-red-500 mt-2">{errorMessage}</p>}
    </div>
  );
};

export default StakeWithdraw;
